$category-main-bg-color: #acd7ec;
$default-padding: 10px;
$default-margin: 10px;
$default-border-radius: 8px;

.kanban-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: $default-padding;
  border-radius: $default-border-radius;

  .kanban-category-main {
    width: max-content;
    max-width: 400px;
    padding: $default-padding;
    margin-top: 20px;

    h4 {
      margin-left: 10px;
    }
  }

  .kanban-category-item {
    padding: $default-padding;
    margin: $default-margin;
  }

  .kanban-add-card {
    padding: $default-padding;
    font-size: 16px;
  }
}


.kanban-category-container {
  overflow: auto;
  max-width: fit-content;
  max-height: 600px;
}

.kanban-category-item {
  max-width: 30;
  display: flex;
  flex-direction: column;
}
